import React, { useState, useEffect } from 'react'
import { 
  Plus, 
  Search, 
  ShoppingCart, 
  Trash2, 
  CreditCard,
  DollarSign,
  Minus,
  Calculator
} from 'lucide-react'
import { useCart } from '../contexts/CartContext'
import Modal from '../components/ui/Modal'
import ConfirmDialog from '../components/ui/ConfirmDialog'
import LoadingSpinner from '../components/ui/LoadingSpinner'
import toast from 'react-hot-toast'
import { formatCurrency } from '../utils/format'
import supabase from '../lib/supabase'
import { useAuth } from '../contexts/AuthContext'
import { dispatchAppEvent } from '../lib/eventBus'
import { useNotifications } from '../contexts/NotificationsContext'

const Sales = () => {
  const { profile, user } = useAuth()
  const { add: addNotification } = useNotifications()
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [showCheckout, setShowCheckout] = useState(false)
  const [paymentMethod, setPaymentMethod] = useState('cash')
  const [amountReceived, setAmountReceived] = useState('')
  const [processing, setProcessing] = useState(false)
  const [confirmClearCart, setConfirmClearCart] = useState(false)
  const [confirmRemoveItemId, setConfirmRemoveItemId] = useState(null)
  
  const { cart, totals, addItem, updateItem, removeItem, clearCart } = useCart()
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  // Load products and categories from database
  const [products, setProducts] = useState([])
  const [categories, setCategories] = useState([])
  const [categoryMap, setCategoryMap] = useState(new Map())

  useEffect(() => {
    let isMounted = true
    const fetchData = async () => {
      try {
        setLoading(true)
        setError(null)

        // Fetch products
        const { data: prodData, error: prodErr } = await supabase
          .from('products')
          .select('id, name, sku, barcode, selling_price, stock_quantity, image_url, category_id')
          .order('name', { ascending: true })

        if (prodErr) throw prodErr

        // Fetch categories to resolve names for filter
        const { data: catData, error: catErr } = await supabase
          .from('categories')
          .select('id, name')
          .order('name', { ascending: true })

        if (catErr) throw catErr

        if (!isMounted) return
        setProducts(prodData || [])
  const catMap = new Map((catData || []).map(c => [c.id, c.name]))
  setCategoryMap(catMap)
  const uniqueCats = Array.from(new Set((prodData || []).map(p => catMap.get(p.category_id)).filter(Boolean)))
  setCategories(['all', ...uniqueCats])
      } catch (e) {
        if (!isMounted) return
        console.error('Failed to load products/categories:', e)
        setError('Failed to load products')
      } finally {
        if (isMounted) setLoading(false)
      }
    }
    fetchData()
    return () => { isMounted = false }
  }, [])

  const filteredProducts = products.filter(product => {
    const matchesSearch = product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         product.sku.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         (product.barcode || '').includes(searchTerm)
    const productCatName = categoryMap.get(product.category_id)
    const matchesCategory = selectedCategory === 'all' || productCatName === selectedCategory
    return matchesSearch && matchesCategory && product.stock_quantity > 0
  })

  const handleQuantityChange = (productId, newQuantity) => {
    if (newQuantity <= 0) {
      setConfirmRemoveItemId(productId)
    } else {
      updateItem(productId, { quantity: newQuantity })
    }
  }

  const handleCheckout = async () => {
    if (cart.items.length === 0) {
      toast.error('Cart is empty')
      return
    }

    if (paymentMethod === 'cash') {
      const received = parseFloat(amountReceived)
      if (!received || received < totals.total) {
        toast.error('Insufficient amount received')
        return
      }
    }

    setProcessing(true)
    
    try {
      if (!profile?.store_id || !user?.id) {
        toast.error('Missing store or user context')
        return
      }

      // Create transaction
      const now = new Date()
      const txNumber = `TX-${now.getFullYear()}${(now.getMonth()+1).toString().padStart(2,'0')}${now.getDate().toString().padStart(2,'0')}-${now.getTime().toString().slice(-6)}`
      const { data: tx, error: txErr } = await supabase
        .from('transactions')
        .insert([{
          transaction_number: txNumber,
          store_id: profile.store_id,
          customer_name: cart.customer?.name || null,
          subtotal: totals.subtotal,
          discount_percentage: totals.discount,
          discount_amount: totals.discountAmount,
          tax_rate: totals.taxRate / 100, // store decimal
          tax_amount: totals.taxAmount,
          total_amount: totals.total,
          payment_method: paymentMethod,
          amount_received: paymentMethod === 'cash' ? parseFloat(amountReceived) : totals.total,
          change_amount: paymentMethod === 'cash' ? Math.max(0, parseFloat(amountReceived) - totals.total) : 0,
          status: 'completed',
          processed_by: user.id,
          processed_at: now.toISOString(),
        }])
        .select()
        .single()

      if (txErr) throw txErr

      // Insert transaction items and update inventory per item
      const itemsPayload = cart.items.map(item => ({
        transaction_id: tx.id,
        product_id: item.product_id,
        product_name: item.name,
        product_sku: item.sku,
        quantity: item.quantity,
        unit_price: item.price,
        total_price: item.price * item.quantity,
      }))

      const { error: itemsErr } = await supabase
        .from('transaction_items')
        .insert(itemsPayload)
      if (itemsErr) throw itemsErr

      // Decrease stock for each product and log inventory change
      for (const item of cart.items) {
        const { data: prod, error: prodErr } = await supabase
          .from('products')
          .select('id, stock_quantity')
          .eq('id', item.product_id)
          .single()
        if (prodErr) throw prodErr
        const prevQty = Number(prod.stock_quantity || 0)
        const newQty = Math.max(0, prevQty - item.quantity)
        const { error: updErr } = await supabase
          .from('products')
          .update({ stock_quantity: newQty, updated_at: new Date().toISOString() })
          .eq('id', item.product_id)
        if (updErr) throw updErr
        await supabase
          .from('inventory_logs')
          .insert([{
            product_id: item.product_id,
            store_id: profile.store_id,
            type: 'stock_out',
            quantity_change: -item.quantity,
            previous_quantity: prevQty,
            new_quantity: newQty,
            reason: 'Sale transaction',
            reference_type: 'sale',
            reference_id: tx.id,
            created_by: user.id,
            created_at: new Date().toISOString(),
          }])
      }

      // Broadcast completion so other pages refresh
      dispatchAppEvent('transaction:completed', { transactionId: tx.id })

      // Create a notification
      try {
        await addNotification({
          title: 'Sale completed',
          message: `${tx.transaction_number} ‚Ä¢ ${formatCurrency(totals.total)}`,
          type: 'success',
          data: { transactionId: tx.id, total: totals.total }
        })
      } catch (e) {
        // non-fatal
      }

      toast.success('Transaction completed successfully!')
      clearCart()
      setShowCheckout(false)
      setAmountReceived('')
      setPaymentMethod('cash')
    } catch (error) {
      console.error('Checkout error:', error)
      toast.error('Transaction failed. Please try again.')
    } finally {
      setProcessing(false)
    }
  }

  const change = paymentMethod === 'cash' && amountReceived 
    ? Math.max(0, parseFloat(amountReceived) - totals.total)
    : 0

  return (
    <div className="h-full flex flex-col lg:flex-row gap-6">
      {/* Product Selection */}
      <div className="flex-1 space-y-6">
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-100">Point of Sale</h1>
          <div className="mt-4 sm:mt-0">
            <span className="text-sm text-gray-500 dark:text-gray-300">
              {filteredProducts.length} products available
            </span>
          </div>
        </div>

        {/* Search and Filters */}
        <div className="flex flex-col sm:flex-row gap-4">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 dark:text-gray-500 w-5 h-5" />
            <input
              type="text"
              placeholder="Search products by name, SKU, or barcode..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="input pl-10"
            />
          </div>
          <select
            value={selectedCategory}
            onChange={(e) => setSelectedCategory(e.target.value)}
            className="input w-full sm:w-auto"
          >
            {categories.map(category => (
              <option key={category} value={category}>
                {category === 'all' ? 'All Categories' : category}
              </option>
            ))}
          </select>
        </div>

        {/* Products Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {loading ? (
            <div className="col-span-full py-12"><LoadingSpinner /></div>
          ) : error ? (
            <div className="col-span-full text-center text-red-600 dark:text-red-400 py-6">{error}</div>
          ) : filteredProducts.map((product) => (
            <div key={product.id} className="card p-4 hover:shadow-md transition-shadow cursor-pointer">
              <div className="aspect-square bg-gray-100 dark:bg-gray-700 rounded-lg mb-3 flex items-center justify-center">
                {product.image_url ? (
                  <img 
                    src={product.image_url} 
                    alt={product.name}
                    className="w-full h-full object-cover rounded-lg"
                  />
                ) : (
                  <div className="text-gray-400 dark:text-gray-500 text-4xl">üì¶</div>
                )}
              </div>
              
              <h3 className="font-medium text-gray-900 dark:text-gray-100 mb-1 line-clamp-2">{product.name}</h3>
              <p className="text-sm text-gray-500 dark:text-gray-400 mb-2">SKU: {product.sku}</p>
              <div className="flex items-center justify-between mb-3">
                <span className="text-lg font-bold text-primary-600">
                  {formatCurrency(product.selling_price)}
                </span>
                <span className="text-sm text-gray-500 dark:text-gray-400">
                  {product.stock_quantity} in stock
                </span>
              </div>
              
              <button
                onClick={() => addItem(product, 1)}
                className="btn btn-primary btn-sm w-full"
                disabled={product.stock_quantity === 0}
              >
                <Plus className="w-4 h-4 mr-1" />
                Add to Cart
              </button>
            </div>
          ))}
        </div>

        {filteredProducts.length === 0 && (
          <div className="text-center py-12">
            <div className="text-gray-400 dark:text-gray-500 text-6xl mb-4">üîç</div>
            <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100 mb-2">No products found</h3>
            <p className="text-gray-500 dark:text-gray-400">Try adjusting your search or filters</p>
          </div>
        )}
      </div>

      {/* Cart Sidebar */}
      <div className="lg:w-80 xl:w-96">
        <div className="card h-full flex flex-col">
          {/* Cart Header */}
      <div className="p-4 border-b border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
        <h2 className="text-lg font-semibold text-gray-900 dark:text-gray-100 flex items-center">
                <ShoppingCart className="w-5 h-5 mr-2" />
                Cart ({totals.itemCount})
              </h2>
        {cart.items.length > 0 && (
                <button
          onClick={() => setConfirmClearCart(true)}
                  className="text-gray-400 hover:text-red-500 transition-colors"
                >
                  <Trash2 className="w-5 h-5" />
                </button>
              )}
            </div>
          </div>

          {/* Cart Items */}
          <div className="flex-1 overflow-y-auto p-4">
            {cart.items.length === 0 ? (
              <div className="text-center py-8">
                <ShoppingCart className="w-12 h-12 text-gray-300 dark:text-gray-600 mx-auto mb-4" />
                <p className="text-gray-500 dark:text-gray-400">Your cart is empty</p>
                <p className="text-sm text-gray-400 dark:text-gray-500">Add products to get started</p>
              </div>
            ) : (
              <div className="space-y-3">
                {cart.items.map((item) => (
                  <div key={item.product_id} className="flex items-center space-x-3 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                    <div className="w-12 h-12 bg-gray-200 dark:bg-gray-600 rounded-lg flex items-center justify-center">
                      <span className="text-xl">üì¶</span>
                    </div>
                    
                    <div className="flex-1 min-w-0">
                      <h4 className="font-medium text-gray-900 dark:text-gray-100 truncate">{item.name}</h4>
                                            <p className="text-sm text-gray-500 dark:text-gray-400">{formatCurrency(item.price)} each</p>
                    </div>
                    
                    <div className="flex items-center space-x-2">
                      <button
                        onClick={() => handleQuantityChange(item.product_id, item.quantity - 1)}
                        className="w-6 h-6 rounded-full bg-gray-200 dark:bg-gray-600 flex items-center justify-center hover:bg-gray-300 dark:hover:bg-gray-500 transition-colors"
                      >
                        <Minus className="w-3 h-3" />
                      </button>
                      
                      <span className="w-8 text-center font-medium">{item.quantity}</span>
                      
                      <button
                        onClick={() => handleQuantityChange(item.product_id, item.quantity + 1)}
                        className="w-6 h-6 rounded-full bg-gray-200 dark:bg-gray-600 flex items-center justify-center hover:bg-gray-300 dark:hover:bg-gray-500 transition-colors"
                      >
                        <Plus className="w-3 h-3" />
                      </button>
                    </div>
                    
                    <button
                      onClick={() => setConfirmRemoveItemId(item.product_id)}
                      className="text-gray-400 dark:text-gray-500 hover:text-red-500 transition-colors"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Cart Summary */}
          {cart.items.length > 0 && (
            <>
              <div className="border-t border-gray-200 dark:border-gray-700 p-4 space-y-2">
                <div className="flex justify-between text-sm">
                  <span>Subtotal:</span>
                  <span>{formatCurrency(totals.subtotal)}</span>
                </div>
                {totals.discount > 0 && (
                  <div className="flex justify-between text-sm text-green-600 dark:text-green-400">
                    <span>Discount ({totals.discount}%):</span>
                    <span>-{formatCurrency(totals.discountAmount)}</span>
                  </div>
                )}
                <div className="flex justify-between text-sm">
                  <span>Tax ({totals.taxRate}%):</span>
                  <span>{formatCurrency(totals.taxAmount)}</span>
                </div>
                <div className="flex justify-between text-lg font-bold border-t dark:border-gray-700 pt-2">
                  <span>Total:</span>
                  <span>{formatCurrency(totals.total)}</span>
                </div>
              </div>

              <div className="p-4 border-t border-gray-200">
                <button
                  onClick={() => setShowCheckout(true)}
                  className="btn btn-primary btn-lg w-full"
                >
                  <Calculator className="w-5 h-5 mr-2" />
                  Checkout
                </button>
              </div>
            </>
          )}
        </div>
      </div>

      {/* Checkout Modal */}
      <Modal
        isOpen={showCheckout}
        onClose={() => setShowCheckout(false)}
        title="Complete Transaction"
        size="md"
      >
        <div className="p-6 space-y-6">
          {/* Order Summary */}
          <div className="bg-gray-50 dark:bg-gray-700 rounded-lg p-4">
            <h3 className="font-medium text-gray-900 dark:text-gray-100 mb-3">Order Summary</h3>
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span>Subtotal:</span>
                <span>{formatCurrency(totals.subtotal)}</span>
              </div>
              {totals.discount > 0 && (
                <div className="flex justify-between text-green-600 dark:text-green-400">
                  <span>Discount:</span>
                  <span>-{formatCurrency(totals.discountAmount)}</span>
                </div>
              )}
              <div className="flex justify-between">
                <span>Tax:</span>
                <span>{formatCurrency(totals.taxAmount)}</span>
              </div>
              <div className="flex justify-between font-bold text-lg border-t dark:border-gray-600 pt-2">
                <span>Total:</span>
                <span>{formatCurrency(totals.total)}</span>
              </div>
            </div>
          </div>

          {/* Payment Method */}
          <div>
            <label className="label">Payment Method</label>
            <div className="grid grid-cols-2 gap-3">
              <button
                onClick={() => setPaymentMethod('cash')}
                className={`p-3 rounded-lg border-2 flex items-center justify-center space-x-2 transition-colors ${
                  paymentMethod === 'cash'
                    ? 'border-primary-500 bg-primary-50 text-primary-700'
                    : 'border-gray-200 hover:border-gray-300'
                }`}
              >
                <DollarSign className="w-5 h-5" />
                <span>Cash</span>
              </button>
              <button
                onClick={() => setPaymentMethod('card')}
                className={`p-3 rounded-lg border-2 flex items-center justify-center space-x-2 transition-colors ${
                  paymentMethod === 'card'
                    ? 'border-primary-500 bg-primary-50 text-primary-700'
                    : 'border-gray-200 hover:border-gray-300'
                }`}
              >
                <CreditCard className="w-5 h-5" />
                <span>Card</span>
              </button>
            </div>
          </div>

          {/* Cash Payment Details */}
          {paymentMethod === 'cash' && (
            <div className="space-y-4">
              <div>
                <label className="label">Amount Received</label>
                <input
                  type="number"
                  step="0.01"
                  value={amountReceived}
                  onChange={(e) => setAmountReceived(e.target.value)}
                  className="input"
                  placeholder="0.00"
                  autoFocus
                />
              </div>
              
              {amountReceived && parseFloat(amountReceived) >= totals.total && (
        <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-lg p-4">
                  <div className="flex justify-between items-center">
          <span className="font-medium text-green-800 dark:text-green-400">Change Due:</span>
          <span className="text-xl font-bold text-green-800 dark:text-green-400">
                      {formatCurrency(change)}
                    </span>
                  </div>
                </div>
              )}
            </div>
          )}

          {/* Action Buttons */}
          <div className="flex space-x-3">
            <button
              onClick={() => setShowCheckout(false)}
              className="btn btn-secondary btn-md flex-1"
              disabled={processing}
            >
              Cancel
            </button>
            <button
              onClick={handleCheckout}
              disabled={processing || (paymentMethod === 'cash' && (!amountReceived || parseFloat(amountReceived) < totals.total))}
              className="btn btn-primary btn-md flex-1"
            >
              {processing ? (
                <>
                  <LoadingSpinner size="sm" />
                  <span className="ml-2">Processing...</span>
                </>
              ) : (
                'Complete Sale'
              )}
            </button>
          </div>
        </div>
      </Modal>

      {/* Confirm clear cart */}
      <ConfirmDialog
        isOpen={confirmClearCart}
        onCancel={() => setConfirmClearCart(false)}
        onConfirm={() => { clearCart(); setConfirmClearCart(false) }}
        title="Clear cart?"
        description="This will remove all items from the cart. You can't undo this action."
        confirmText="Clear"
        variant="danger"
      />

      {/* Confirm remove single item */}
      <ConfirmDialog
        isOpen={confirmRemoveItemId != null}
        onCancel={() => setConfirmRemoveItemId(null)}
        onConfirm={() => { if (confirmRemoveItemId != null) removeItem(confirmRemoveItemId); setConfirmRemoveItemId(null) }}
        title="Remove item?"
        description="This item will be removed from the cart."
        confirmText="Remove"
        variant="danger"
      />
    </div>
  )
}

export default Sales
